.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* width: 300px; */
    /* max-width: 400px; */
    width: 100%;
    height: 130px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
    align-items: center;
    overflow: hidden;
}

.subtotal__gift {
    display: flex;
    align-items: center;
}

.subtotal__gift>input {
    margin-right: 5px;
}

